function Stacked2(props) {

    const [loc, setLoc] = useState(["/TOP"])

    const defaultLocation = "/TOP";
    const [state, setState] = useState({
        currentLocation: [defaultLocation],
        myKey: "/TOP",
        chartOptions: {
            chart: {
                type: "column", // column / bar
                zoomType: "xy",
            },
            title: { text: defaultLocation },
            credits: { enabled: false },
            xAxis: {
                title: {
                    text: "PET Number",
                },
                categories: props.inputdata[defaultLocation].xvals
            },
            yAxis: {
                min: 0,
                allowDecimals: true,
                title: {
                    text: "Time (s)",
                },
            },
            legend: {
              enabled: true,
              reversed: true,
              itemStyle: { fontSize: "12pt" },
            },
            plotOptions: {
                series: {
                    stacking: "normal",
                    events: {
                        click(e){
                            drillDown(this.name);
                        },
                    },
                },
            },
            series: props.inputdata[defaultLocation].yvals,
            navigation: {
                menuStyle: {
                    background: "#E0E0E0",
                }
            }
        }
    });



    const drillDown = (name) => {
        console.debug("drillDown2 incoming loc = ", loc)
        const newLoc = [...loc, name]
        console.debug(" --> new loc = ", newLoc)

        const key = newLoc.join("/")
        console.debug(" --> key = ", key)

        setLoc(newLoc);

        setState ({
            currentLocation: newLoc,
            myKey: key,
            chartOptions: {
                title: { text: key },
                xAxis: {
                    categories: props.inputdata[key].xvals,
                },
                series: props.inputdata[key].yvals,
                //plotOptions: {
                //    series: {
                //        stacking: "normal",
                //        events: {
                //            click(e){
                //                drillDown(this.name);
                //            },
                //        },
                //    },
                //},
            }
        });

        //setState(newState);

        console.debug("after state = ", state.currentLocation, state.myKey)
    };

    const updateSeries = () => {

        /*
        const key = "/TOP"
        console.debug("updateSeries key = ", key, props.inputdata)
        const chartData = {
            chartOptions: {
                title: { text: key },
                xAxis: {
                    categories: props.inputdata[key].xvals,
                },
                series: props.inputdata[key].yvals,
            }
        };
        setState(chartData);
        */

        console.debug("current state = ", state, "loc =", loc)
    }

    const dd1 = () => {
        drillDown("[EARTH Grid Comp] RunPhase1")
    }

    const dd2 = () => {
        drillDown("[ATM] RunPhase1")
    }

    return (
      <div>
        <HighchartsReact
          highcharts={Highcharts}
          options={state.chartOptions}
        />
      <button onClick={updateSeries.bind(this)}>See State</button>
      <button onClick={dd1.bind(this)}>EARTH</button>
      <button onClick={dd2.bind(this)}>ATM</button>

      </div>
    )

}

export default Stacked2

